.search-container {
 
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  .search-input {
    width: 100%;
    height: 46px;
    padding: 8px 12px;
    border-radius: 23px;
    font-size: 20px;
    font-weight: bolder;
    border: 1px solid #868686;
  }
  .search-button {
    position: absolute;
    background: transparent;
    border: none;
    left: 8px;
    top: 50%;
    cursor: pointer;
    transform: translateY(-43%);
    i {
      font-size: 30px;
      color: $orange1;
      text-shadow: 1px 2px 1px $gray2;
    }
  }
}
